.popup {
  display: flex;
  visibility: hidden;
  box-sizing: border-box;
  height: 100vh;
  width: 100%;
  max-width: 100vw;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 19px;
  opacity: 0;
  transition: visibility .5s, opacity .5s ease;
}
